.public-debate {
  padding-top: 64px;

  @media (min-width: $tablet-width) {
    padding-top: 87px;
    padding-bottom: 26px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 8px;
  }
}

.public-debate__head {
  margin-bottom: 8px;
  display: flex;
  flex-flow: row wrap;

  @media (min-width: $tablet-width) {
    margin-bottom: 16px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.public-debate__title-icon {
  margin-top: 6px;
  margin-right: 17px;

  @media (min-width: $tablet-width) {
    margin-right: 20px;
  }
}

.public-debate__title {
  margin: 0 0 32px;
  padding: 0;
  max-width: 80%;

  @media (min-width: $tablet-width) {
    max-width: 65%;
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.public-debate__archive-link {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
    margin: 18px 0 0 auto;
    align-self: flex-start;
    flex-shrink: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 13px;
  }

  span {
    padding-right: 12px;
  }
}

.public-debate .description {
  margin: 0;
}

.public-debate__description {
  min-width: 100%;
}

.public-debate__list {
  margin: 0 0 32px;
  padding: 0;
  list-style: none;

  @media (min-width: $tablet-width) {
    margin-bottom: 48px;
  }
}

.public-debate__item {
  padding: 24px 0;

  border-bottom: 1px solid var(--color-stroke, $light-grey);

  @media (min-width: $desktop-width) {
    padding-bottom: 32px;
    display: grid;
    grid-template-columns: 1fr 211px;
    grid-column-gap: 56px;
    grid-template-areas:
      "a    c"
      "b    c";

    &:hover:not(.focus-visible) .button-star {
      opacity: 1;
      display: block;

      &:hover {
        color: var(--color-interactive-accent, $blue-accent);
      }
    }
  }

  &>* {
    align-self: start;
  }
}

.public-debate__date-wrapper {
  margin-bottom: 24px;

  display: flex;
  flex-flow: row wrap;

  grid-area: a;

  @media (min-width: $tablet-width) {
    margin-bottom: 16px;
  }
}

.public-debate__date {
  margin-bottom: 4px;

  @media (max-width: $tablet-width - 1) {
    min-width: 100%;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-right: 14px;
  }
}

.public-debate__status {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: 12px;

  @media (min-width: $tablet-width) {
    position: relative;
    padding-left: 9px;
  }

  @media (max-width: $tablet-width - 1) {
    padding-left: 0;

    &::before {
      display: none;
    }
  }
}

.public-debate__joined {
  display: none;

  &--visible {
    display: block;
  }
}

.public-debate__link {
  margin-bottom: 24px;
  grid-area: b;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    justify-self: start;
  }
}

.public-debate__period-wrapper {

  @media (min-width: $desktop-width) {
    margin-top: 20px;
  }
}

.public-debate__button {
  padding-right: 20px;

  display: flex;
  justify-content: center;
  flex-flow: row nowrap;
  align-items: center;

  font-size: rem(14px);
  line-height: rem(19px);

  span {
    flex-shrink: 1;
    padding: 0;
    padding-left: 12px;
  }

  svg {
    margin-left: 12px;
    margin-bottom: 3px;
    flex-shrink: 0;
  }

  @media (max-width: $tablet-width - 1) {
    margin-bottom: 32px;
  }

  @media (min-width: $tablet-width) {
    font-size: rem(15px);
    line-height: rem(20px);

    max-width: 539px;
    margin: 0 auto;

    justify-content: flex-start;

    span {
      padding-left: 0;
    }
  }
}

.public-debate__archive-mobile {
  display: flex;
  justify-content: center;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.public-debate--all {
  padding: 24px 0 64px;

  @media (min-width: $tablet-width) {
    padding-top: 40px;
    padding-bottom: 80px;
  }

  .public-debate__back-btn {
    margin-left: 2px;

    font-size: rem(18px);
    line-height: 130%;

    @media (min-width: $tablet-width) {
      margin-bottom: 40px;
    }
  }

  .public-debate__title {
    font-size: rem(30px);
    line-height: rem(36px);

    @media (min-width: $tablet-width) {
      max-width: unset;
      font-size: rem(40px);
      line-height: rem(47px);
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 40px;
    }
  }

  .public-debate__description {
    margin-bottom: 24px;

    @media (min-width: $tablet-width) {
      margin-bottom: 40px;
    }
  }

  .public-debate__select {
    display: flex;
    flex-flow: row wrap;
    align-items: center;

    border: 1px solid var(--color-stroke, $light-grey);
    border-radius: 5px;

    @media (max-width: $tablet-width - 1) {
      margin-bottom: 16px;
      flex-flow: column wrap;
      align-items: flex-start;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 16px;
    }

    .ss-single-selected {
      padding: 15px 16px 15px 23px;

      @media (max-width: $tablet-width - 1) {
        padding: 15px 16px;
        justify-content: center;
      }

      .placeholder {
        .ss-disabled {
          font-size: rem(15px);
          line-height: 135%;
        }
      }
    }

    .placeholder {
      flex: 0 0 auto;
      width: auto;

      font-size: rem(15px);
      line-height: 135%;

      @media (max-width: $tablet-width - 1) {
        flex-shrink: 1;
      }
    }

    .ss-content {
      top: 41px;
      left: 0;
      width: 219px;

      background-color: #ffffff;
    }

    .ss-main .ss-single-selected .ss-arrow {
      margin-left: 12px;
    }

    .ss-main .ss-single-selected .ss-arrow span {
      width: 8px;
      height: 8px;
    }

    .form-select__checkbox-wrapper {
      padding: 23px;
      margin: 0 0 0 auto;
      font-size: rem(15px);
      line-height: 135%;

      label {
        position: relative;
      }

      @media (max-width: $tablet-width - 1) {
        padding: 15px 16px;
        margin-left: 0;
      }
    }
  }

  .public-debate__list {
    margin-bottom: 57px;

    @media (min-width: $tablet-width) {
      margin-bottom: 48px;
    }
  }

  .public-debate__paginator {
    margin-top: 0;
    margin-bottom: 56px;

    @media (min-width: $desktop-width) {
      margin-bottom: 80px;
    }
  }

  .public-debate__archive {
    padding: 24px;

    @media (min-width: $tablet-width) {
      .archive-link__hidden {
        display: none;
      }
    }

    @media (min-width: $desktop-width) {
      margin: 0 auto;
      padding: 22px 24px;
      max-width: 1035px;
    }

    .archive-link__btn {
      min-height: 21px;
    }
  }
}
